import { autoWebViewJs } from "@droid/android"
import { Box } from "@mui/material";
import { useEffect, useState } from "react"

function getVersion(){
    const script = `
    var version = -1;
    try{
        var context = com.fanfanlo.lib.mc.libMainModel.application;
        packageInfo = context.packageManager.getPackageInfo(context.packageName, 0)
        version = packageInfo.longVersionCode
    }catch(e){
    }
    
    version;
    `
    const script2 = `
    var version = com.fanfanlo.emergencycall.utils.AppUtils.getAppVersionCode();
    version;
    `
    const script3 = `
    
        var context = com.fanfanlo.lib.mc.libMainModel.application.applicationContext;
        packageInfo = context.packageManager.getPackageInfo(context.packageName, 0)
        var version = packageInfo.longVersionCode;
        version;
    `
    const {javaResultString} = autoWebViewJs.callScript(script2);
    console.log('vvvvvvvvvvvvvvvvvvvv32v2', javaResultString);
    return javaResultString || "0";
}


export function AppVersion(){
    const [version, setVersion] = useState("0");
    const [isNew, setIsNew] = useState(true)
    const newVersion = "6"
    function checkVersion(){
        const v = getVersion();
        setVersion(v);
        setIsNew(v == newVersion)
    }
    useEffect(()=>{
        checkVersion();
    })
    
    return(
        <Box>
            <Box>
                当前版本:{version}
            </Box>
            {!isNew && (
                <Box>
                    <Box>
                        您可以复制下面链接，然后打开浏览器下载最新版的app。
                    </Box>
                    
                <Box>
                    https://cdn.qiniu.fanfanlo.com/apk/emc/zh-cn/app-mainlandChina-release.7.apk
                </Box>
                </Box>
            )}
        </Box>
    )
}